import React from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getCountries } from "../actions";
import {Link} from "react-router-dom";
import Card from "./Card";

export default function Home () {
    const dispatch = useDispatch()
    const allCountries = useSelector((state) => state.countries)

    useEffect(()=>{
        dispatch(getCountries());
    },[dispatch])

    function handleClick(e){
        e.preventDefault();
        dispatch(getCountries()); 
    }

    return (
        <div>
            <Link to = "/country">Crear Pais</Link>
            <h1>Paises del mundo</h1>
            <button onClick={e=> {handleClick(e)}}>Refrescar</button>
            <div>
                <select>
                    <option value="asc">Ascendente</option>
                    <option value="desc">Descendente</option>
                </select>
                <select>
                    <option value="All">Todos</option>
                    <option value="Cont">Continente</option>
                    <option value="Act">Actividad</option>
                </select>
                <select>
                    <option value="all">Todos</option>
                    <option value="db">Creadas</option>
                </select>
        {
            allCountries && allCountries.map(e => {
                return(
                <div key={e.id}>
                <Card img={e.imgflag} name= {e.name} continente={e.continents} id={e.id}/>
                </div>
            )})
        }
            </div>
        </div>
    )

}